
.toolbar{
    position: fixed;
    background: transparent linear-gradient(180deg, #4303D6 0%, #5E03AB 100%) 0% 0% no-repeat padding-box;
    border-radius: 0px 0px 70px 0px; 
 }

.toolbar_navigation_items a:hover,
.toolbar_navigation_items a:active{
   color:yellow
}

.Logout{
     position: fixed;
     left:20px;
    top:584px;
}

.iconName{
    font-size:10px;
    font: Regular Helvetica;
    text-align: center;
}

.toggleButton{
    position: absolute;
    top:32px;
    height:14px;
    width:24px;
    float:none;
    left:30%;
    
}

.DashboardIcon{
    color:white;
    position: absolute;
    top:102px;
    float:none;
    left:20%;
    text-align: center;
}

.DashboardImg{
    height:27.29px;
    width:40.6px;
}

.LibraryIcon{
    color:white;
    position: absolute;
    top:199px;
    text-align: center;
    float: none;
    left:15%;

}

.LibraryImg{
    height: 32px;
    width: 32px;
}

.NotificationIcon{
    color:white;
    position: absolute;
    top:302px;
    text-align: center;
    float: none;
    left:20%;
}

.NotificationImg{
    height: 31px;
    width:26px;
}
