.whitebox{
    background: #FFFFFF 0% 0% no-repeat padding-box;
    border: 1px solid #707070;
    opacity: 1;
    padding-bottom: 2rem;
}
.coloredBox{
    transform: matrix(0, -1, 1, 0, 0, 0);
    background: transparent linear-gradient(180deg, #5103BE 0%, #5D03AB 100%) 0% 0% no-repeat padding-box;
    border: 1px solid #707070;
    opacity: 1;
}

.insideColoredBox{

    text-align: center;
    font-family: Helvetica;
    letter-spacing: 0;
    color: #FFFFFF;
    transform: rotate(90deg);
    justify-content: space-between;
}
.containerLine{   
    justify-content: space-between;
    border-top: 4px solid  #B1B8C2;
    background: 0% 0% no-repeat padding-box;
  }
 
  .containerLine div {
    font-weight: bold;
    background: #5D03AB 0% 0% no-repeat padding-box;
    text-align: center;
    border-radius: 50%;
  }  

#first-tick-mark {
    position: relative;
    display: inline-block;
}
#next-tick-mark {
    position: relative;
    display: inline-block;

}

#first-tick-mark::before {
    position: absolute;
    background-color: #FFFFFF;
    content: "";
    transform: translateX(10px) rotate(-45deg);
    transform-origin: left bottom;
}

#first-tick-mark::after {
    position: absolute;
    background-color: #FFFFFF;
    content: "";
    transform: translateX(10px) rotate(-45deg);
    transform-origin: left bottom;
}

#next-tick-mark::before {
    position: absolute;
    background-color: #FFFFFF;
    content: "";
    transform: translateX(10px) rotate(-45deg);
    transform-origin: left bottom;
}
#next-tick-mark::after {
    position: absolute;
    background-color: #FFFFFF;
    content: "";
    transform: translateX(10px) rotate(-45deg);
    transform-origin: left bottom;
}

.containerLabel{
    display: flex;
    text-align: center;
    border-top: 2px solid  #FFFFFF;
    background: 0% 0% no-repeat padding-box;
  
}
.containerLabel span{
    font-family : Helvetica;
    color:#5C03AC;
    letter-spacing: 0;
    text-align: center;
    font-weight: bold;
}

.containerServices{  
    display: flex;
    text-align: center;
    border-top: 2px solid  #FFFFFF;
    background: 0% 0% no-repeat padding-box;
}
.containerServicesofZenAnalytica{
    display: flex;
    text-align: center;
    border-top: 2px solid  #FFFFFF;
    background: 0% 0% no-repeat padding-box;
}
.services{
       
    display:inline-block;
}

.services1{
       
    font-weight: bold;
    background: transparent linear-gradient(180deg, #4303D6 0%, #5E03AB 100%) 0% 0% no-repeat padding-box;
    box-shadow: 0px 3px 6px #00000029;
    line-height: 40px;
    text-align: center;
    border-radius: 50%;
    display:inline-block;
    text-align: "center";
    font-family: Arial, Helvetica, sans-serif;
    color: #FFFFFF;

  }
