
.dividerLine{
  overflow: hidden;
  background-color: #F6F5FD;
  position: fixed;
  
}

.rectangelBox{
  opacity: 0.29;
  display: inline-block;

}

.vl {
  width: 0.5926513671875px;
  border: 1px solid #707070;
  opacity: 1;
  display: inline-block;
}
.boldTab{
  top: 0px;
  left: 0.3176422119140625px;
  text-align: center;
  letter-spacing: 0px;
  color: #2D3642;
  display: inline-block;
  
}

.thinTab{
  top: 0px;
  text-align: center;
  font-weight: lighter;
  font-family: Helvetica;
  letter-spacing: 0px;
  display: inline-block;
  color: #2D3642;

}
.title{
      display: inline-block;
      text-align: right;
      right:0;
      top:0;
    }
.titleName{
  font-family: Helvetica;
  font-weight:bolder;
}

.titleDescription{
  font-family: Helvetica;
  font-style: oblique;
}
@media (min-width: 1025px) {
    /*************************desktop**************************/
  .dividerLine{
    width: 825px;
    height: 70px;
    
  }
  .rectangelBox{
    top: 327px;
    left: 301px;
    width: 200px;
    height: 23px;
    margin-left: 7rem;
  }
    .vl {
      margin-left: 5rem;
      top: 350px;
      left: 776px;
      height: 45px;
    }
  
    .boldTab{
      width:93px;
      height: 23px;
      font: Bold 23px/36px Helvetica;
    }

    .thinTab{
      height: 23px;
      margin-left:5px;
      font-size: 20px;
      font-family:  Helvetica;
    }
  
    
      
    .logoImg{
      margin-top: 10px;
      padding-right: 32rem;
      padding-left:5rem;
      display: inline-block;
      width: 149px;
      height: 58px;
      background: url('../img/Logo.png') 0% 0% no-repeat padding-box;
    }

    .titleName{

      font-size: 20px;
    }

    .titleDescription{
 
      font-size: 13px;
    }
}

